<template>
  <div class="top">
    <div>
      <i class="iconfont icon-tishi"></i>
      <!--<i :class="className" style="font-size: 0.2rem;margin-right: 0.1rem"></i>-->
      <span class="hint">{{msg}}</span>
      
    </div>
      
  </div>
</template>
<script>
  export default{
    data(){
      return{
        className:'',
        name:'',
        msg:''
      }
    },
    mounted(){
      let name=this.$route.name;
      name = name.indexOf('hotel')!==-1?name.substring(5).toLowerCase():name
      switch(name){
        case 'goods':
            this.className='iconfont icon-pricetag';
            this.name='';
            this.msg='';
            break;
        case 'info':
          this.className='iconfont icon-xinxi';
          this.name='';
           this.msg='';
          break;
        case 'pay':
          this.className='iconfont icon-icon-wx-pay';
          this.name='';
          this.msg='';
          break;
        case 'style':
          this.className='iconfont icon-style-edit';
          this.name='';
          this.msg=' ';
          break;
        case 'card':
          this.className='iconfont icon-vip-';
          this.name='';
          this.msg=' ';
          break;
        case 'sorts':
          this.className='iconfont icon-quanbushangpinfenlei';
          this.name='';
          this.msg='';
          break;
        case 'delivery':
          this.className='iconfont icon-peisong';
          this.name='';
          this.msg='';
          break;
        case 'inform':
          this.className='iconfont icon-bell-hl';
          this.name='';
          this.msg='';
          break;
        case 'bookInform':
          this.className='iconfont icon-bell-hl';
          this.name='';
          this.msg='';
          break;
        case 'bookStyle':
          this.className='iconfont icon-tishi';
          this.name='';
          this.msg='';
          break;
      }
    }
  }
</script>
<style scoped="scoped">
  @import url("../../static/base.css");
</style>



